import React, { useState, useEffect, useRef } from "react";
import { cw, ch, rad } from "./layout";

const Board = ({ figures, theme, setReady }) => {
  const [status, setStatus] = useState(0);
  const canvasRef = useRef();
  const imageRef = useRef(new Map());
  const countRef = useRef();
  const voiceRef = useRef();
  const renderParams = ["#852", "#459", "#137"];

  const drawDot = (ctx, rect1, rect2, spec) => {
    ctx.strokeStyle = renderParams[theme];
    ctx.lineWidth = 2.5;
    ctx.beginPath();
    const shft = 1.5;
    const len = 7;
    for (let i = 0; i < 4; i++) {
      let x = i === 0 || i === 3 ? rect1[0] - shft : rect1[0] + 2 * rad + shft;
      let y = i === 0 || i === 1 ? rect1[1] - shft : rect1[1] + 2 * rad + shft;
      let v;
      if (i === 0) {
        v = [x + len, y, x, y, x, y + len];
      } else if (i === 1) {
        v = [x - len, y, x, y, x, y + len];
      } else if (i === 2) {
        v = [x - len, y, x, y, x, y - len];
      } else {
        v = [x + len, y, x, y, x, y - len];
      }
      ctx.moveTo(v[0], v[1]);
      ctx.lineTo(v[2], v[3]);
      ctx.lineTo(v[4], v[5]);
    }
    ctx.stroke();
    ctx.fillStyle = "rgba(255, 255, 255, 0.3)";
    const delta = 10;
    ctx.fillRect(rect2[0] + delta, rect2[1] + delta, 2 * rad - 2 * delta, 2 * rad - 2 * delta);
    if (voiceRef.current) {
      const audio = voiceRef.current.get(spec === 2 ? "check" : spec === 1 ? "kill" : "click");
      audio.play();
    }
  };

  const drawImage = () => {
    const board = imageRef.current.get("board");
    const ctx = canvasRef.current.getContext("2d");
    ctx.drawImage(board, 0, 0);
    if (figures && figures.items) {
      figures.items.forEach((item) => {
        const img = imageRef.current.get(item[0]);
        if (img) {
          ctx.drawImage(img, item[1], item[2]);
        }
      });
    }
    if (figures && figures.mark) {
      drawDot(ctx, figures.hi, figures.mark, figures.spec);
    }
  };

  useEffect(() => {
    const imgs = [
      ["board", theme === 1 ? "plain" : theme === 2 ? "modern" : "classic"],
      ["K", "KRed"],
      ["k", "kBlack"],
      ["A", "ARed"],
      ["a", "aBlack"],
      ["B", "BRed"],
      ["b", "bBlack"],
      ["R", "RRed"],
      ["r", "rBlack"],
      ["N", "NRed"],
      ["n", "nBlack"],
      ["C", "CRed"],
      ["c", "cBlack"],
      ["P", "PRed"],
      ["p", "pBlack"],
    ];
    setStatus(0);
    setReady(0);
    if (!voiceRef.current) {
      const voices = new Map();
      voices.set("click", new Audio("/assets/click.wav"));
      voices.set("check", new Audio("/assets/jiang.wav"));
      voices.set("kill", new Audio("/assets/chi.wav"));
      voiceRef.current = voices;
    }
    countRef.current = 0;
    imgs.forEach((o, i) => {
      const img = new Image();
      const prefix = o[0] === "board" ? "" : theme === 1 ? "p" : theme === 2 ? "m" : "c";
      img.src = `/assets/${prefix}${o[1]}.png`;
      // console.log("Loading " + img.src + " " + i + " of " + total);
      img.onload = () => {
        //console.log("Loaded " + img.src);
        countRef.current++;
        imageRef.current.set(o[0], img);
        // console.log("Image loaded " + imageRef.current.size);
        if (countRef.current === imgs.length) {
          setStatus(1);
          setReady(1);
        }
      };
    });
  }, [theme]);

  useEffect(() => {
    if (status === 1) {
      drawImage();
    }
  }, [figures, status]);

  return (
    <>
      {status === 0 ? (
        <h4>Loading...</h4>
      ) : (
        <canvas
          ref={canvasRef}
          width={cw}
          height={ch}
          style={{ display: "inline-block", whitespace: "nowrap" }}
          tabIndex={2}
        />
      )}
    </>
  );
};

export default Board;
