import React, { useState, useEffect, useRef } from "react";

import { books, sublists } from "./data";
import "./bookChooser.css";

const BookChooser = ({ book, bookDialog, setSelect, setBookDialog }) => {
  const columns = 4;
  const modalRef = useRef();

  const tableData = () => {
    const vs = books();
    const cats = [];
    for (let i = 0; i < 3; i++) {
      const key = i === 0 ? "对局" : i === 1 ? "古谱" : "攻略";
      const items = [];
      vs.forEach((e) => {
        if (e.type === key) {
          items.push(e.name);
        }
      });
      const cat = i === 0 ? "对弈名局" : i === 1 ? "古籍珍本" : "通关攻略";
      cats.push({
        sid: i === 0 ? "a" : i === 1 ? "b" : "c",
        type: cat,
        list: sublists(items, columns),
      });
    }
    return cats;
  };

  const link = (item) => {
    const hi = item === book;
    const clr = hi ? "#00f" : "#223";
    const font = hi ? "bold" : "normal";
    return { margin: 10, color: clr, fontWeight: font };
  };

  const cell = (id, list, base) => {
    return list.map((item, inx) => (
      <td key={id + (base + inx)}>
        <a
          style={link(item)}
          href="#"
          onClick={() => {
            //alert(item + " is selected");
            select(item);
          }}
        >
          {item}
        </a>
      </td>
    ));
  };

  const line = (id, list) => {
    return list.map((item, inx) => <div>{cell(id, item, inx * list[0].length)}</div>);
  };

  const block = (cat) => {
    return (
      <>
        <tr key={cat.sid + "Header"}>
          <th colSpan={columns}>{cat.type}</th>
        </tr>
        {cat.list.map((item, inx) => (
          <tr key={cat.sid + inx}>{cell(cat.sid, item, inx * columns)}</tr>
        ))}
      </>
    );
  };

  const select = (val) => {
    modalRef.current.close();
    setBookDialog(0);
    if (val && val !== book) {
      setSelect({ book: val, index: 0 });
    }
  };
  useEffect(() => {
    if (bookDialog === 1) {
      modalRef.current.showModal();
    }
  }, [bookDialog]);
  return (
    <>
      <dialog ref={modalRef}>
        <h2>棋谱总目录</h2>
        <table>
          <tbody>{tableData().map((row) => row.list.length > 0 && block(row))}</tbody>
        </table>
        <div style={{ textAlign: "right" }}>
          <button onClick={() => select(null)}>返回</button>
        </div>
      </dialog>
    </>
  );
};
export default BookChooser;
