import React, { useState, useEffect, useRef } from "react";

const ThemeChooser = ({ theme, themeDialog, onThemeChange, setThemeDialog }) => {
  const modalRef = useRef();
  const prefix = (use) => {
    return use ? "继续" : "改为";
  };

  const select = (val) => {
    modalRef.current.close();
    setThemeDialog(0);
    if (val !== theme) {
      onThemeChange(val);
    }
  };

  useEffect(() => {
    if (themeDialog === 1) {
      modalRef.current.showModal();
    }
  }, [themeDialog]);
  const style = { padding: "1em" };
  const button = { padding: "0.5em", fontSize: "1.2em" };

  return (
    <dialog
      style={{ border: 0, backgroundColor: "#e7e7f7", padding: "1em 2em 2em 2em" }}
      ref={modalRef}
    >
      <h2>选择棋盘风格</h2>
      <div>
        <div style={style}>
          <button style={button} onClick={() => select(0)}>
            {prefix(theme === 0) + "使用古典风格"}
          </button>
        </div>
        <div style={style}>
          <button style={button} onClick={() => select(1)}>
            {prefix(theme === 1) + "使用俭朴风格"}
          </button>
        </div>
        <div style={style}>
          <button style={button} onClick={() => select(2)}>
            {prefix(theme === 2) + "使用现代风格"}
          </button>
        </div>
      </div>
    </dialog>
  );
};

export default ThemeChooser;
