import React, { useState, useEffect, useRef } from "react";
import Cookies from "js-cookie";
import BookChooser from "./bookChooser";
import GameChooser from "./gameChooser";
import { buildList } from "./algo";
import { game4Book } from "./data";
import { ch } from "./layout";

const Title = ({ ready, anim, isPlaying, game, setGame }) => {
  const canvasRef = useRef();
  const cookie = Cookies.get("chess-game");
  const [select, setSelect] = useState(
    cookie ? JSON.parse(cookie) : { book: "适情雅趣", index: 0 }
  );
  const [bookDialog, setBookDialog] = useState(0);
  const [gameDialog, setGameDialog] = useState(0);
  const right = 40;
  const left = 54;

  useEffect(() => {
    if (select) {
      setGame(buildGame(select.book, select.index));
      Cookies.set("chess-game", JSON.stringify(select));
    }
  }, [select]);

  useEffect(() => {
    const ctx = canvasRef.current.getContext("2d");
    const wd = canvasRef.current.width;
    const hg = canvasRef.current.height;
    ctx.clearRect(0, 0, wd, hg);
    if (ready === 0) {
      return;
    }
    if (game) {
      let txt = game.book;
      let x = left;
      let y = 40;
      ctx.lineWidth = 2;
      ctx.font = "bold 48px sans-serif";
      for (let i = 0; i < txt.length; i++) {
        const ch = txt.substring(i, i + 1);
        ctx.fillStyle = "red";
        ctx.fillText(ch, x, y);
        ctx.strokeStyle = "#ffa";
        ctx.strokeText(ch, x, y);
        y += 60;
      }
      txt = game.name;
      const lines = [];
      for (let i = 0, n = txt.length; i < n; ) {
        const c = txt.charCodeAt(i);
        if (c > 256) {
          lines.push(txt.substring(i, i + 1));
          i++;
        } else {
          let k = n;
          for (let j = i + 1; j < n; j++) {
            const c = txt.charCodeAt(j);
            if (c > 256) {
              k = j;
              break;
            }
          }
          lines.push(txt.substring(i, k));
          i = k;
        }
      }
      x = wd - right;
      y = hg - 5;
      ctx.font = "bold 26px Times New Roman";
      ctx.fillStyle = "#8ef";
      for (let i = lines.length - 1; i >= 0; i--) {
        const str = lines[i];
        ctx.fillText(str, x - ctx.measureText(str).width / 2, y);
        y -= 36;
      }
      txt = game.black;
      if (txt) {
        ctx.font = "bold 30px sans-serif";
        let x = wd - ctx.measureText(txt).width - 16;
        let grd = ctx.createLinearGradient(100, 0, 0, 24);
        grd.addColorStop(0, "#330");
        grd.addColorStop(1, "#030");
        ctx.lineWidth = 1.2;
        ctx.fillStyle = grd;
        ctx.fillText(txt, x, 32);
        ctx.strokeStyle = "white";
        ctx.strokeText(txt, x, 32);
      }
    }
    if (!anim && !isPlaying) {
      let grd = ctx.createLinearGradient(24, 0, 0, 24);
      grd.addColorStop(0, "#a93");
      grd.addColorStop(1, "#940");
      ctx.fillStyle = grd;
      ctx.beginPath();
      ctx.moveTo(left - 40, 0);
      ctx.lineTo(left - 16, 0);
      ctx.lineTo(left - 28, 20);
      ctx.fill();
      grd = ctx.createLinearGradient(24, 0, 0, 24);
      grd.addColorStop(0, "#39a");
      grd.addColorStop(1, "#046");
      ctx.fillStyle = grd;
      ctx.beginPath();
      ctx.moveTo(wd - right - 55, hg);
      ctx.lineTo(wd - right - 31, hg);
      ctx.lineTo(wd - right - 43, hg - 20);
      ctx.fill();
    }
  }, [anim, game, isPlaying, ready]);

  const handleClick = (e) => {
    if (!anim && !isPlaying) {
      const x = e.clientX - canvasRef.current.offsetLeft;
      const y = e.clientY - canvasRef.current.offsetTop;
      const w = canvasRef.current.width;
      if (x < 50 && y < 50) {
        setBookDialog(1);
      } else if (
        select &&
        Math.abs(x - (w - right - 43)) < 50 &&
        y > canvasRef.current.height - 60
      ) {
        setGameDialog(1);
      }
    }
  };
  const buildGame = (book, index) => {
    const data = game4Book(book);
    const now = data[index];
    const list = buildList(now[2], now[3]);
    return {
      index: index,
      book: book,
      name: now[0],
      list: list,
      res: now[1] === "未知" || now[1].length === 0 ? null : now[1],
      red: now.length > 4 ? now[4] : null,
      black: now.length > 5 ? now[5] : null,
    };
  };

  //"第2关", "未知", "3k5/4RR3/9/9/9/9/9/9/3r1p3/4K4 w", "f8f9"
  return (
    <>
      <canvas
        ref={canvasRef}
        width={250}
        height={ch}
        style={{ display: "inline-block", float: "right", whitespace: "nowrap" }}
        tabIndex={1}
        onClick={handleClick}
      />
      <BookChooser
        book={select.book}
        bookDialog={bookDialog}
        setSelect={setSelect}
        setBookDialog={setBookDialog}
      />
      <GameChooser
        book={select.book}
        index={select.index}
        gameDialog={gameDialog}
        setSelect={setSelect}
        setGameDialog={setGameDialog}
      />
    </>
  );
};

export default Title;
