import React, { useState, useEffect, useRef } from "react";

const RateControl = ({ duration, setDuration }) => {
  const canvasRef = useRef();
  const time0 = Math.log(30000);
  const timeMax = Math.log(500);
  const x0 = 30;
  const xMax = 160;
  const ymeter = 54;

  const x4 = (time) => {
    return x0 + ((Math.log(time) - time0) * (xMax - x0)) / (timeMax - time0);
  };

  const time4 = (x) => {
    const val = time0 + ((x - x0) * (timeMax - time0)) / (xMax - x0);
    return Math.round(Math.exp(val));
  };

  useEffect(() => {
    const ctx = canvasRef.current.getContext("2d");
    const wd = canvasRef.current.width;
    const hg = canvasRef.current.height;
    ctx.clearRect(0, 0, wd, hg);
    let grd = ctx.createLinearGradient(160, 14, 30, 26);
    grd.addColorStop(0, "#9a3");
    grd.addColorStop(1, "#490");
    ctx.strokeStyle = grd;
    ctx.lineWidth = 3;
    ctx.beginPath();
    ctx.moveTo(30, ymeter - 8);
    ctx.lineTo(30, ymeter + 4);
    ctx.moveTo(160, ymeter - 8);
    ctx.lineTo(160, ymeter + 4);
    ctx.moveTo(30, ymeter);
    ctx.lineTo(160, ymeter);
    ctx.stroke();
    ctx.beginPath();
    ctx.stroke();
    ctx.fillStyle = grd;
    ctx.arc(x4(duration), ymeter, 8, 0, 2 * Math.PI, false);
    ctx.fill();
    ctx.font = "16px Times New Roman";
    ctx.fillText("慢", 13, ymeter - 15);
    ctx.fillText("快", 153, ymeter - 15);
    ctx.font = "12px Times New Roman";
    ctx.fillStyle = "#777";
    ctx.fillText("点击想要的速度", 52, ymeter + 25);
  }, [duration]);

  const handleClick = (e) => {
    const x = e.clientX - canvasRef.current.offsetLeft;
    const y = e.clientY - canvasRef.current.offsetTop;
    if (Math.abs(y - ymeter) < 12 && x > x0 + 4 && x < xMax - 4) {
      if (x > x0 + 4 && x < xMax - 4) {
        const nv = time4(x);
        if (Math.abs(nv - duration) > 200) {
          console.log("New duration " + nv);
          setDuration(nv);
        }
      }
    }
  };

  return (
    <>
      <canvas
        ref={canvasRef}
        width={250}
        height={100}
        style={{ display: "inline-block", float: "right", whitespace: "nowrap" }}
        tabIndex={4}
        onClick={handleClick}
      />
    </>
  );
};

export default RateControl;
