import React, { useState, useEffect, useRef } from "react";
import ThemeChooser from "./themeChooser";
import { ch } from "./layout";

const Info = ({ ready, anim, isPlaying, game, step, theme, onThemeChange }) => {
  const canvasRef = useRef();
  const [themeDialog, setThemeDialog] = useState(0);
  const limit = 34;
  const shift = 50;

  const pick = (list, num) => {
    const res = [];
    let start = Math.max(1, num - limit);
    if (start % 2 === 0) {
      start += 1;
    }
    for (let i = start; i <= num; i++) {
      res.push({ index: i, note: list[i].note });
    }
    return res;
  };

  const drawLine = (ctx, x, y, txt, end, red) => {
    ctx.font = end ? "bold 16px serif" : "16px serif";
    ctx.fillStyle = end ? (red ? "#f00" : "black") : red ? "#700" : "#666";
    ctx.fillText(txt, x, y);
  };

  const renderList = (ctx) => {
    if (!game || !step || step >= game.list.length) return;
    const lines = pick(game.list, step);
    const total = lines.length;
    if (total > 0) {
      const wid = canvasRef.current.width - shift;
      let x = shift + 20;
      let y = 26;
      let grd = ctx.createLinearGradient(wid, 0, shift, canvasRef.current.height - 60);
      grd.addColorStop(0, "#e0ffff");
      grd.addColorStop(1, "#b8e6f0");
      const even = grd;
      grd = ctx.createLinearGradient(wid, 0, shift, canvasRef.current.height - 60);
      grd.addColorStop(0, "#ffffe0");
      grd.addColorStop(1, "#f0e6b8");
      const odd = grd;
      const rows = (total % 2 === 1 ? total + 1 : total) / 2;
      for (
        let i = 0, k = 0, j = total === 0 ? 0 : (lines[0].index + 1) / 2;
        i < total;
        i += 2, j++, k++
      ) {
        ctx.fillStyle = j % 2 === 0 ? even : odd;
        const d = k === 0 ? y - 24 : y - 19;
        let h = k === 0 || k === rows - 1 ? 31 : 26;
        if (total <= 2) {
          h += 5;
        }
        ctx.fillRect(shift + 2, d, wid - 4, h);
        ctx.font = "18px sans-serif";
        ctx.fillStyle = "#44b";
        ctx.fillText("" + j, x, y);
        drawLine(ctx, x + 48, y, lines[i].note, i === total - 1, true);
        if (i + 1 < total) {
          drawLine(ctx, x + 145, y, lines[i + 1].note, i + 1 === total - 1, false);
        }
        y += 26;
      }
    }
  };

  useEffect(() => {
    const wd = canvasRef.current.width;
    const hg = canvasRef.current.height;
    const ctx = canvasRef.current.getContext("2d");
    ctx.clearRect(0, 0, wd, hg);
    if (ready === 0 || !game) {
      return;
    }
    renderList(ctx);
    const hasName = game && game.red;
    if (hasName) {
      ctx.font = "bold 30px sans-serif";
      ctx.lineWidth = 1.2;
      ctx.fillStyle = "red";
      ctx.fillText(game.red, 34, hg - 4);
      ctx.strokeStyle = "white";
      ctx.strokeText(game.red, 34, hg - 4);
    }
    if (game && game.res) {
      ctx.font = "16px serif";
      ctx.fillStyle = "#aaa";
      const x = !hasName ? 30 : wd - ctx.measureText(game.res).width;
      ctx.fillText(game.res, x, hg - 26);
    }
    if (game && game.list) {
      const n = game.list.length;
      const a = (n % 2 === 1 ? n + 1 : n) / 2 + "回合";
      ctx.font = "16px serif";
      ctx.fillStyle = "#aaa";
      const x = !hasName ? 30 : wd - ctx.measureText(a).width;
      ctx.fillText(a, x, hg - 2);
    }
    if (!anim && !isPlaying) {
      let grd = ctx.createLinearGradient(24, 0, 0, 4);
      grd.addColorStop(0, "#aaa");
      grd.addColorStop(1, "#777");
      ctx.fillStyle = grd;
      let x = 14;
      let y = 4;
      for (let i = 0; i < 3; i++) {
        ctx.fillRect(x, y, 24, 3.5);
        y += 9;
      }
    }
  }, [ready, game, anim, isPlaying, step]);

  const handleClick = (e) => {
    if (!anim && !isPlaying) {
      const x = e.clientX - canvasRef.current.offsetLeft;
      const y = e.clientY - canvasRef.current.offsetTop;
      if (x < 50 && y < 45) {
        setThemeDialog(1);
      }
    }
  };

  return (
    <>
      <canvas
        ref={canvasRef}
        width={300}
        height={ch}
        style={{ display: "inline-block", float: "left", whitespace: "nowrap" }}
        tabIndex={3}
        onClick={handleClick}
      />
      <ThemeChooser
        theme={theme}
        themeDialog={themeDialog}
        onThemeChange={onThemeChange}
        setThemeDialog={setThemeDialog}
      />
    </>
  );
};

export default Info;
