import React, { useState, useEffect, useRef } from "react";
import { game4Book, sublists } from "./data";
import "./gameChooser.css";

const GameChooser = ({ book, index, gameDialog, setGameDialog, setSelect }) => {
  const modalRef = useRef();
  const [total, setTotal] = useState();
  const [longList, setLongList] = useState(false);
  const [games, setGames] = useState([]);
  // const index = game && game.index ? game.index : 0;

  const link = (inx) => {
    const hi = inx === index;
    const clr = hi ? "#00f" : "#223";
    const font = hi ? "bold" : "normal";
    return { color: clr, fontWeight: font };
  };

  const select = (val) => {
    modalRef.current.close();
    setGameDialog(0);
    let newInx = val === -1 ? (index === total - 1 ? 0 : index + 1) : val;
    if (newInx >= 0 && newInx !== index) {
      setSelect({ book: book, index: newInx });
    }
  };

  const createRow = (row, base) => {
    return row.map((cell, inx) => (
      <td key={"cell" + base + inx}>
        <a
          style={link(base + inx)}
          href="#"
          onClick={() => {
            // alert(base + inx + " " + cell[0] + " is selected");
            select(base + inx);
          }}
        >
          {cell[0]}
        </a>
      </td>
    ));
  };

  const buttons = () => {
    return (
      <div>
        <button className="button" onClick={() => select(-1)}>
          下一个
        </button>
        <button className="button" onClick={() => select(-2)}>
          取消
        </button>
      </div>
    );
  };

  useEffect(() => {
    if (gameDialog === 1) {
      const data = game4Book(book);
      const n = data.length;
      const cols = n < 10 ? 3 : n < 20 ? 5 : n < 50 ? 6 : n < 120 ? 8 : 12;
      setTotal(n);
      setLongList(n > 10 * cols);
      setGames(sublists(data, cols));
      modalRef.current.showModal();
    }
  }, [gameDialog]);

  return (
    <>
      <dialog ref={modalRef}>
        <h2>本集棋谱目录</h2>
        {buttons()}
        <div>
          <table>
            <tbody>
              {games.map((row, inx) => (
                <tr key={"row" + inx}>{createRow(row, inx * games[0].length)}</tr>
              ))}
            </tbody>
          </table>
        </div>
        {longList ? buttons() : <></>}
      </dialog>
    </>
  );
};
export default GameChooser;
