import React, { useState, useEffect, useRef } from "react";
import { ReactComponent as Play } from "./assets/play.svg";
import { ReactComponent as Pause } from "./assets/pause.svg";
import { ReactComponent as Next } from "./assets/next.svg";
import { ReactComponent as Prev } from "./assets/prev.svg";
import { ReactComponent as First } from "./assets/first.svg";
import { ReactComponent as Last } from "./assets/last.svg";

import "./navigate.css";

const Navigate = ({
  isPlaying,
  anim,
  step,
  game,
  onFirstClick,
  onPrevClick,
  onNextClick,
  onLastClick,
  onPlayPauseClick,
}) => {
  const [check, setCheck] = useState({ back: false, forward: false });

  useEffect(() => {
    if (!game || !game.list || isPlaying) {
      setCheck({ back: false, forward: false });
    } else {
      const len = game.list.length;
      setCheck({ back: step > 0, forward: step < len - 1 });
    }
  }, [game, step, isPlaying, anim]);

  const buttonNext = () => {
    return (
      <button
        type="button"
        disabled={!check.forward}
        className="nav"
        aria-label="Next"
        onClick={onNextClick}
      >
        <Next />
      </button>
    );
  };

  const buttonLast = () => {
    return (
      <button
        type="button"
        disabled={!check.forward}
        className="nav"
        aria-label="Last"
        onClick={onLastClick}
      >
        <Last />
      </button>
    );
  };

  const buttonPrev = () => {
    return (
      <button
        type="button"
        disabled={!check.back}
        className="nav"
        aria-label="Prev"
        onClick={onPrevClick}
      >
        <Prev />
      </button>
    );
  };

  const buttonFirst = () => {
    return (
      <button
        type="button"
        disabled={!check.back}
        className="nav"
        aria-label="First"
        onClick={onFirstClick}
      >
        <First />
      </button>
    );
  };

  const buttonMiddle = () => {
    return isPlaying ? (
      <button
        type="button"
        className="control"
        onClick={() => onPlayPauseClick(false)}
        aria-label="Pause"
      >
        <Pause />
      </button>
    ) : (
      <button
        type="button"
        className="control"
        onClick={() => onPlayPauseClick(true)}
        aria-label="Play"
      >
        <Play />
      </button>
    );
  };

  return (
    <div className="audio-controls">
      {buttonFirst()}
      {buttonPrev()}
      {buttonMiddle()}
      {buttonNext()}
      {buttonLast()}
    </div>
  );
};

export default Navigate;
