const FEN_OPEN = "rnbakabnr/9/1c5c1/p1p1p1p1p/9/9/P1P1P1P1P/1C5C1/9/RNBAKABNR w";

const xpos4 = (x, side) => {
  return posVal(side === 0 ? 8 - x : 9 + x);
};

const pos4 = (index, side) => {
  return posVal(side === 0 ? index : 9 + index);
};

const posVal = (index) => {
  return "一二三四五六七八九１２３４５６７８９".charAt(index);
};

const xvalue = (ch) => {
  return ch >= 97 ? ch - 97 : ch - 48;
};

const label4 = (v) => {
  var i = "RNBAKCPrnbakcp".indexOf(v);
  return "车马相仕帅炮兵车马象士将炮卒".charAt(i);
};

const secondPart = (v, x0, y0, x1, y1) => {
  var side = v.charCodeAt(0) < 97 ? 0 : 1;
  var dir = y0 === y1 ? 0 : side === 0 ? (y1 > y0 ? -1 : 1) : y1 > y0 ? 1 : -1;
  if ("RCKPrckp".includes(v)) {
    return dir === 0
      ? "平" + xpos4(x1, side)
      : (dir === -1 ? "退" : "进") + pos4(Math.abs(y1 - y0) - 1, side);
  }
  return (dir === -1 ? "退" : "进") + xpos4(x1, side);
};

const itemAtRow = (t) => {
  for (var e = [], n = 0, r = 0; r < t.length && !(8 < n); r++) {
    var a = t.charAt(r);
    if (/^\d+$/.test(a)) {
      for (var s = 0; s < a && ((e[n] = ""), s++, !(8 < ++n)); );
    } else {
      e[n] = a;
      n++;
    }
  }
  return e;
};

const relativePos = (v, rows, x0, y0) => {
  for (var y = 0; y < 10; y++) {
    if (y !== y0 && rows[y][x0] === v) {
      var b = v.charCodeAt(0) < 97 ? y > y0 : y < y0;
      return b ? "前" : "后";
    }
  }
  return null;
};

function fenOf(t) {
  let e = "";
  let n = 0;
  for (let i = 0; i < t.length; i++) {
    if (t[i] === "") {
      n++;
    } else {
      if (0 === n) {
        e += t[i];
      } else {
        e += n + t[i];
        n = 0;
      }
    }
  }
  return n === 0 ? e : e + n;
}

const fenToRows = (fen) => {
  var arry = fen.split(" ")[0].split("/");
  var rows = new Array(10);
  for (var i = 0; i < 10; i++) {
    rows[i] = itemAtRow(arry[i]);
  }
  return rows;
};

const rows2Fen = (arry) => {
  let a = "";
  for (var i = 0; i < 10; i++) {
    if (i > 0) {
      a += "/";
    }
    a += fenOf(arry[i]);
  }
  return a;
};

const fenAfterMove = (fen, move) => {
  const arry = fen.split(" ")[0].split("/");
  const x0 = xvalue(move.charCodeAt(0)),
    y0 = move.charAt(1),
    x1 = xvalue(move.charCodeAt(2)),
    y1 = move.charAt(3);
  const rows = new Array(10);
  for (let i = 0; i < 10; i++) {
    rows[i] = itemAtRow(arry[i]);
  }
  var start = rows[9 - y0];
  var moving = start[x0];
  const kill = rows[9 - y1][x1];
  var rop = relativePos(moving, rows, x0, 9 - y0);
  start[x0] = "";
  if (y0 === y1) {
    start[x1] = moving;
    arry[9 - y0] = fenOf(start);
  } else {
    arry[9 - y0] = fenOf(start);
    rows[9 - y1][x1] = moving;
    arry[9 - y1] = fenOf(rows[9 - y1]);
  }
  const check = checkMat(rows, "RNBAKCP".includes(moving));
  const exp1 = !rop
    ? label4(moving) + xpos4(x0, moving.charCodeAt(0) < 97 ? 0 : 1)
    : rop + label4(moving);
  return {
    fen: arry.join("/"),
    move: [x1, 9 - y1, x0, 9 - y0],
    check: check,
    kill: kill,
    note: exp1 + secondPart(moving, x0, 9 - y0, x1, 9 - y1),
  };
};

const buildList = (fen, moves) => {
  const len = moves.length / 4;
  const list = new Array(len + 1);
  let theFen = !fen || fen.length === 0 ? FEN_OPEN : fen;
  list[0] = { fen: theFen, move: [-1, -1, 0, 0], note: "" };
  for (var i = 1, j = 0; i <= len; i++, j += 4) {
    var item = fenAfterMove(theFen, moves.substring(j, j + 4));
    list[i] = item;
    theFen = item.fen;
  }
  return list;
};

const board = (fen) => {
  const list = [];
  fenToRows(fen).forEach((v, row) => {
    for (let col = 0; col < 9; col++) {
      if (v[col]) {
        list.push([v[col], col, row]);
      }
    }
  });
  return list;
};

const countCols = (col1, col2, row, board) => {
  if (col1 > col2) return countCols(col2, col1, row, board);
  let n = 0;
  for (let col = col1 + 1; col < col2; col++) {
    if (board[row][col]) {
      n++;
    }
  }
  return n;
};

const countRows = (col, row1, row2, board) => {
  if (row1 > row2) return countRows(col, row2, row1, board);
  let n = 0;
  for (let row = row1 + 1; row < row2; row++) {
    if (board[row][col]) {
      n++;
    }
  }
  return n;
};

const byKing = (col, row, x, y, board) => {
  return col === x && countRows(col, row, y, board) === 0;
};

const byRookCanon = (col, row, x, y, between, board) => {
  return (
    (col === x && countRows(col, row, y, board) === between) ||
    (row === y && countCols(col, x, row, board) === between)
  );
};

const byKnight = (col, row, x, y, board) => {
  return (
    (Math.abs(col - x) === 1 && Math.abs(row - y) === 2 && !board[(y + row) / 2][x]) ||
    (Math.abs(row - y) === 1 && Math.abs(col - x) === 2 && !board[y][(x + col) / 2])
  );
};

const byPawn = (col, row, x, y, red) => {
  return (
    (y === row && Math.abs(col - x) === 1) ||
    (x === col && ((red && y === row + 1) || (!red && y === row - 1)))
  );
};

const checkMat = (board, red) => {
  let col = -1;
  let row = -1;
  LOOP: for (let j = 0; j < 10; j++) {
    for (let i = 0; i < 9; i++) {
      if ((red && board[j][i] === "k") || (!red && board[j][i] === "K")) {
        col = i;
        row = j;
        break LOOP;
      }
    }
  }
  for (let y = 0; y < 10; y++) {
    for (let x = 0; x < 9; x++) {
      const z = board[y][x];
      if (z && ((red && "KRNCP".includes(z)) || (!red && "krncw".includes(z)))) {
        if ((z === "K" || z === "k") && byKing(col, row, x, y, board)) {
          return true;
        }
        if (((red && z === "R") || (!red && z === "r")) && byRookCanon(col, row, x, y, 0, board)) {
          return true;
        }
        if (((red && z === "N") || (!red && z === "n")) && byKnight(col, row, x, y, board)) {
          return true;
        }
        if (((red && z === "C") || (!red && z === "c")) && byRookCanon(col, row, x, y, 1, board)) {
          return true;
        }
        if (((red && z === "P") || (!red && z === "p")) && byPawn(col, row, x, y, red)) {
          return true;
        }
      }
    }
  }
  return false;
};

export { FEN_OPEN, buildList, board };

