import React, { useState, useEffect, useRef } from "react";

const Progress = ({ anim, isPlaying, step, game, setStep }) => {
  const canvasRef = useRef();
  const x0 = 35;
  const xMax = 215;
  const ymeter = 40;
  const x4 = (current, totalStep) => {
    return Math.round(x0 + (current * (xMax - x0)) / totalStep);
  };

  const pos4 = (x, totalStep) => {
    return Math.round(((x - x0) * totalStep) / (xMax - x0));
  };

  useEffect(() => {
    const ctx = canvasRef.current.getContext("2d");
    const wd = canvasRef.current.width;
    const hg = canvasRef.current.height;
    ctx.clearRect(0, 0, wd, hg);
    const v4total = game ? game.list.length - 1 : 0;
    if (v4total && v4total > 3) {
      const v4step = step;
      const pos = x4(v4step, v4total);
      let grd = ctx.createLinearGradient(30, ymeter + 7, 160, ymeter + 7);
      grd.addColorStop(0, "#990");
      grd.addColorStop(1, "#ba2");
      ctx.fillStyle = grd;
      ctx.beginPath();
      ctx.moveTo(x0, ymeter - 7);
      ctx.lineTo(x0, ymeter + 7);
      ctx.lineTo(pos, ymeter + 7);
      ctx.lineTo(pos, ymeter - 7);
      ctx.fill();
      grd = ctx.createLinearGradient(160, ymeter - 7, 30, ymeter + 7);
      grd.addColorStop(0, "#552");
      grd.addColorStop(1, "#220");
      ctx.fillStyle = grd;
      ctx.beginPath();
      ctx.moveTo(pos, ymeter - 7);
      ctx.lineTo(pos, ymeter + 7);
      ctx.lineTo(xMax, ymeter + 7);
      ctx.lineTo(xMax, ymeter - 7);
      ctx.fill();
      grd = ctx.createLinearGradient(160, ymeter - 7, 30, ymeter + 7);
      grd.addColorStop(0, "#ffa");
      grd.addColorStop(1, "#aa9");
      ctx.fillStyle = grd;
      ctx.beginPath();
      ctx.moveTo(Math.max(x0, pos - 5), ymeter - 11);
      ctx.lineTo(Math.max(x0, pos - 5), ymeter + 11);
      ctx.lineTo(Math.min(xMax, pos + 5), ymeter + 11);
      ctx.lineTo(Math.min(xMax, pos + 5), ymeter - 11);
      ctx.fill();
      ctx.font = "14px Times New Roman";
      ctx.fillText("0", x0 - 14, ymeter + 5);
      ctx.fillText("" + v4total, xMax + 6, ymeter + 5);
      if (v4step > 0 && v4step < v4total) {
        ctx.fillStyle = "#77f";
        const t = "" + v4step;
        ctx.fillText(t, pos - ctx.measureText(t).width / 2, ymeter - 17);
      }

      ctx.font = "12px Times New Roman";
      ctx.fillStyle = "#777";
      ctx.fillText("点击想要的位置", 92, ymeter + 33);
    }
  }, [step, game]);

  const handleClick = (e) => {
    if (!anim && !isPlaying && step && game) {
      const total = game.list.length;
      if (total > 4) {
        const x = e.clientX - canvasRef.current.offsetLeft;
        const y = e.clientY - canvasRef.current.offsetTop;
        if (x > x0 + 4 && x < xMax - 4 && Math.abs(y - ymeter) < 12) {
          if (Math.abs(pos4(x, total) - step) > 2) {
            setStep(pos4(x, total));
          }
        }
      }
    }
  };

  return (
    <>
      <canvas
        ref={canvasRef}
        width={240}
        height={100}
        style={{ display: "inline-block", float: "right", whitespace: "nowrap" }}
        tabIndex={4}
        onClick={handleClick}
      />
    </>
  );
};

export default Progress;
